body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background:#553055;
}

.App{
  background:#4c2a4c;
  max-width:700px;
  width:90%;
  margin: 20px auto;
  text-align:center;
}
nav{
  padding:10px 20px;
  text-align:center;
  background:#6d3d6d
}
nav h1 {
  margin:10px 0;
}
.book-list{
  padding:20px;
}
.book-list ul{
  padding:0;
  list-style-type:none;
}
.book-list li{
  background:#6d3d6d;
  padding:10px;
  margin: 10px 0;
  border-radius: 4px;
  cursor:pointer;  
}

.book-list li:hover{
  opacity:0.7;
  text-decoration:line-through;
}

.book-list .title{
  font-weight:bold;
  color:#fff;
  font-size:1.2rem;
}
.book-list .author{
  font-size:0.9rem;
  color:#ddd;
}
.empty{
  margin:20px;
  text-align:center; 
}

form{
  padding:20px;
}
input{
  width:100%;
  padding:10px;
  box-sizing: border-box;
  margin:6px 0;
  background:#3c1f3c;
  color:#fff;
  border:0;
}
button[type='submit']{
  margin:10px auto;
  background:#eee;
  border:0;
  padding:6px 20px;
  display:block;         
}